'use strict'

import log from 'common/log'
import {isChrome, isFirefox} from 'common/utils'

import EmitterMixin from 'common/emitter'
import MediaSink from 'media/mediaSink'
import StreamSource from 'media/streamSource'
import HtmlSink from 'media/htmlSink'
import StreamSink from 'media/streamSink'
import DummySource from 'media/dummySource'
import {ProxySource, PeerMediaPipe, DataShare} from 'webrtc/components'

const TAG = 'tab-sharing-sink'

const replacement = Symbol('sink-replacement')

/**
 * A media sink that renders to a target HTML media element, either `<audio>` or `<video>`.
 *
 * @class HtmlSink
 * @extends MediaSink
 * @param {Object} [options] - Optional options object.
 * @param {external:HTMLMediaElement} target - The target HTML element.
 */
export default class TabSharingSink extends EmitterMixin(MediaSink) {
  constructor({target = null, iceServers, call, onStream}) {
    super()
    if (onStream) {
      this._onStream = onStream
    }
    this._sinkSource = new StreamSource()
    this._tabSink = new HtmlSink()
    let bail = 0
    while (target && target[replacement] && bail < 5) {
      target = target[replacement]
      bail += 1
    }
    if (bail >= 5) {
      log.error(TAG, 'HtmlSink got stuck in element replacement loop')
    }
    this._target = target
    this._iceServers = iceServers
    this._call = call
    this._tabSink.target = target
    this._refreshSink = this._refreshSink.bind(this)
    this._refreshTick = null
  }

  /**
   * The target HTML element to render to.
   *
   * This can be updated at any time.
   *
   * @member {external:HTMLMediaElement} HtmlSink#target
   */
  get target() {
    return this._target
  }

  set target(target) {
    if (this._target) {
      this._cleanup()
    }
    this._target = target || null
    if (target) {
      this._assign()
    }
  }

  connectSink() {
    this._proxySource = new ProxySource({
      connectionConfig: {
        iceServers: this._iceServers,
        iceCandidateFilter(info) {
          return info.type === 'relay'
        },
      },
    })
    this._proxySourceForwarder = new StreamSink({onStream: this._onStream.bind(this)})
    this._proxySource.connect(this._proxySourceForwarder)
    this._call.attach('content-proxy', this._proxySource)

    this._streamSource = new PeerMediaPipe()
    if (isChrome) {
      this._dummySource = new DummySource({video: true}) // So that recvonly chrome -> firefox works
      this._dummySource.connect(this._streamSource)
    }

    this._mediaSourceForwarder = new StreamSink({onStream: this._onStream.bind(this)})
    this._streamSource.connect(this._mediaSourceForwarder)
    this._call.attach('content-stream', this._streamSource)

    this._remoteVideoSizer = new DataShare()
    this._remoteVideoSizer.on('update:size', this._onRemoteVideoSize)
    this._call.attach('content-size', this._remoteVideoSizer)

    this._scroller = new DataShare()
    this._call.attach('scroller', this._scroller)
    this._scroller.set('scroll', {x: 0, y: 0})

    this._onStream = this._onStream.bind(this)
    this._cleanup = this._cleanup.bind(this)
  }

  cleanSink()	{
    return this._cleanup()
  }

  _hasTarget(target) {
    if (this._target === target) {
      return true
    } else if (this._target === target[replacement]) {
      return true
    }
    return false
  }

  // eslint-disable-next-line
  /**
   * @private
   */
  _onStream(stream) {
    if (this._onStream) {
      this._onStream(stream)
    }
    this._emit('stream', stream)
  }

  _handleWindowResize() {
  }

  _onRemoteVideoSize() {

  }

  _cleanup() {
    if (this._target && (this._target instanceof HTMLMediaElement)) {
      if (this._target.srcObject) {
        this._target.srcObject = null
      } else if (this._target.src) {
        this._target.src = ''
      }
    } else {
      this._target = window.attachMediaStream(this._target, null)
    }

    if (this._dummySource) {
      this._dummySource.disconnect(this._streamSource)
      try {
        this._dummySource.stop()
      } catch (error) { /* libcct 0.18.7 is a bit broken */ }
      this._dummySource = null
    }
    this._proxySource.disconnect(this._proxySourceForwarder)
    this._streamSource.disconnect(this._mediaSourceForwarder)
    if (!this._call.closed) {
      this._call.detach('scroller', this._scroller)
      this._call.detach('content-proxy', this._proxySource)
      this._call.detach('content-stream', this._streamSource)
      this._call.detach('content-size', this._remoteVideoSizer)
    }
    this._remoteVideoSizer.off('update:size', this._onRemoteVideoSize)
    this._remoteVideoSizer = null
    this._source = null
    this._scroller = null
    this._proxySource = null
    this._streamSource = null
    this._proxySourceForwarder = null
    this._mediaSourceForwarder = null
  }

  _assign() {
    if (this.stream) {
      try {
        let originalElement = this._target
        this._target = window.attachMediaStream(this._target, this.stream)
        if (originalElement instanceof HTMLMediaElement) {
          originalElement[replacement] = this._target
        }
      } catch (error) {
        log.info(TAG, `Error when assigning html sink stream: ${error}`)
      }
      try {
        let result = this._target.play()
        if (result && result.catch) {
          result.catch(error => {
            log.debug(TAG, `Failed to play stream, ${error}`)
          })
        }
      } catch (error) {
        log.info(TAG, `Error when playing html sink stream: ${error}`)
      }
    }
  }

  _refreshSink() {
    if (this._target) {
      this._cleanup()
      this._assign()
    }
  }
}



